<template>
<div class="bg-gradient-primary">
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-xl-10 col-lg-12 col-md-9">
        <div class="card o-hidden border-0 shadow-lg my-5">
          <div class="card-body p-0">
            <div class="row">
              <div class="col-lg-6 d-none d-lg-block bg-password-image"></div>
              <div class="col-lg-6">
                <div class="p-5">
                  <div class="text-center">
                    <h1 class="h4 text-gray-900 mb-2">Forgot Your Password?</h1>
                    <p class="mb-4">We get it, stuff happens. Just enter your email address below and we'll send you a link to reset your password!</p>
                  </div>
                  <form class="user">
                    <div class="form-group">
                      <input type="name" class="form-control form-control-user" id="exampleInputFirstName" required v-model="user.firstname" placeholder="Enter FirstName if remember">
                    </div>
                    <div class="form-group">
                      <input type="name" class="form-control form-control-user" id="exampleInputLastName" required v-model="user.lastname" placeholder="Enter LastName if remember">
                    </div>
                    <div class="form-group">
                      <input type="email" class="form-control form-control-user" id="exampleInputEmail" required v-model="user.email" aria-describedby="emailHelp" placeholder="Enter Email Address..."/>
                    </div>
                    <div>
                      <button class="btn btn-primary btn-user btn-block" v-on:click="handleResetClick()">
                        Reset Password</button>
                    </div>
                  </form>
                  <hr>
                  <div class="text-center">
                    <a class="small"><router-link :to="{path:'/register'}">Create an Account!</router-link></a>
                  </div>
                  <div class="text-center">
                    <a class="small"><router-link :to="{path:'/'}">Already have an account? Login!</router-link></a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import http from '../../http-common.js'
import router from '../../router/index.js'
// import axios from 'axios'

export default {
  name: 'forget_password',
  data () {
    return {
      user: {
        loading: true
      },
      submitted: false
    }
  },
  methods: {
    handleResetClick () {
      var data = {
        id: 1,
        firstname: this.user.firstname,
        lastname: this.user.lastname,
        email: this.user.email,
        password: '',
        active: true
      }

      http
        .post('/login', data)
        .then(response => {
          if (response.data) {
            router.push({ path: '/' })
          } else {
            console.log('Email don\'t exist.')
          }
        }).finally(() => this.loading = false)
        .catch(e => {
          console.log(e)
        })

      this.submitted = true
    }
  }
}
</script>

<style>

</style>



