import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/login/Login.vue'
import Register from '../components/login/Register.vue'
import ForgetPassword from '../components/login/ForgetPassword.vue'
import Table from '../components/bookTable/Table.vue'
import About from '../components/bookTable/About.vue'
// import Cart from '../components/shopCart/Cart.vue'
import Manage from '../components/manage/Manage.vue'
import Dialog from '../components/Dialog.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'login',
      alias: '/login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/forgetpw',
      name: 'forget_password',
      component: ForgetPassword
    },
    {
      path: '/manage',
      name: 'manage',
      component: Manage
    },
    {
      path: '/table',
      name: 'table',
      component: Table
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/dialog',
      name: 'dialog',
      component: Dialog
    }
  ]
})



